<template>
  <div class="container">
    <div class="page-head"></div>
    <div class="container-box">
      <div class="search-box">
        <div class="search-box-input">
          <el-input v-model="searchData.keyword" class="demo-input-suffix" placeholder="" clearable @keyup.enter.native="getList">
            <i slot="suffix" class="el-input__icon el-icon-search" @click="getList"></i>
          </el-input>
          <el-select v-model="searchData.is_public" @change="getList">
            <el-option label="" value=""></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
          <el-select v-model="searchData.role" @change="getList">
            <el-option label="" value=""></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </div>
      </div>
      <div class="card-box" v-loading="loading">
        <div class="card-warpper">
          <div class="w7-card"
            v-for="(item,index) in docList" :key="index"
            @click="goChapter(item.id)">
            <div class="w7-card-title">{{item.name}}</div>
            <div class="w7-card-time">{{format(item.operator.time)}} {{item.operator.name}}</div>
            <div class="icon-box">
              <el-tooltip effect="dark" content="" placement="bottom" v-if="!item.is_public">
                <i class="wi wi-lock"></i>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="bottom" v-if="item.acl.has_manage">
                <i class="wi wi-transfer" @click.stop="transferDoc(item.id)"></i>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="bottom">
                <i class="wi wi-view" @click.stop="readDoc(item.id)"></i>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="bottom" v-if="item.acl.has_manage">
                <i class="wi wi-guanli" @click.stop="settingDoc(item.id)"></i>
              </el-tooltip>
            </div>
          </div>
          <div class="w7-card add-btn"  @click="dialogDocInfoVisible = true">
            <div class="add-text"></div>
            <div class="add-box">
              <i class="el-icon-circle-plus"></i>
            </div>
          </div>
        </div>
      </div>
      <el-pagination
        background
        @current-change = "getList"
        layout="prev, pager, next, total"
        prev-text=""
        next-text=""
        :current-page.sync = "currentPage"
        :page-count="pageCount"
        :hide-on-single-page = "true"
        style="margin-top:20px"
      >
      </el-pagination>
      <!--  -->
      <el-dialog class="we7-dialog" title="" :visible.sync="dialogDocInfoVisible" :close-on-click-modal="false" center>
        <el-form label-width="105px" label-position="left">
          <el-form-item label="">
            <el-input v-model="name" autocomplete="off"></el-input>
          </el-form-item>
          <el-radio-group v-model="radio" class="ownership">
            <el-radio v-model="radio" label="1"></el-radio>
            <el-radio v-model="radio" label="2"></el-radio>
          </el-radio-group>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="createDoc"> </el-button>
          <el-button @click="dialogDocInfoVisible = false"> </el-button>
        </div>
      </el-dialog>
      <el-dialog class="we7-dialog" title="" :visible.sync="dialogTransferDoc" :close-on-click-modal="false" center>
        <div class="transfer-tip">
          <i class="el-icon-warning"></i>
        </div>
        <el-form label-width="115px" label-position="left">
          <el-form-item label="">
            <el-input v-model="transferUsername" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="transfer"> </el-button>
          <el-button @click="dialogTransferDoc = false"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { timestampFormat } from '@/utils/utils'
export default {
  name: 'docIndex',
  data() {
    return {
      radio: '1',
      loading: false,
      searchData: {
        keyword: '',
        role: '',
        is_public: ''
      },
      docList: [],
      currentPage: 0,//
      page_size: 29,
      pageCount: 0,//
      total: 0,//
      name: '',
      dialogDocInfoVisible: false,//
      actClass:"actClass",
      dialogTransferDoc: false,
      transferUsername: '',
      selectDocId: ''
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.loading = true
      this.$post('/admin/document/all',{
        page: this.currentPage,
        page_size: this.page_size,
        ...this.searchData
      })
        .then(res => {
          this.docList = res.data
          this.pageCount = res.page_count
          this.total = res.total
          this.loading = false
        })
    },
    createDoc() {
      this.$post('/admin/document/create', {
        name: this.name,
        is_public: this.radio
      })
        .then(res => {
          this.$message('')
          this.dialogDocInfoVisible = false
          this.goChapter(res, true)
        })
    },
    removeDoc(id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$post('/admin/document/delete',{
          id: id
        })
          .then(() => {
            this.getList()
            this.$message('')
          })
      })
    },
    updateDoc(id, isShow) {
      this.$post('/admin/document/update',{
          id: id,
          is_show: isShow == 1 ? 2 : 1
        })
          .then(() => {
            //docList
            this.docList.forEach(row => {
              if(row.id == id) {
                row.is_show = isShow == 1 ? 2 : 1
                return
              }
            });
          })
    },
    readDoc(id) {
      let routeUrl = this.$router.resolve({
        path: "/chapter/" + id
      })
      window.open(routeUrl.href, '_blank')
    },
    settingDoc(id) {
      let routeUrl = this.$router.resolve({
        path: "/admin/document/" + id
      })
      window.open(routeUrl.href, '_blank')
    },
    transferDoc(id) {
      this.selectDocId = id
      this.transferUsername = ''
      this.dialogTransferDoc = true
    },
    transfer() {
      this.$post('/admin/document/change-founder',{
          document_id: this.selectDocId,
          username: this.transferUsername
        })
          .then(() => {
            this.getList()
            this.$message('')
            this.dialogTransferDoc = false
          })
    },
    goChapter(id, bool) {
      let routeData = {
        name: 'chapter',
        params: {
          id: id
        }
      }
      if (bool) {
        routeData['query'] = { type: 'add'}
      }
      this.$router.push(routeData)
    },
    format(time) {
      if (time) {
        let result = timestampFormat(time)
        if (result.length > 8) {
          return timestampFormat(time).slice(5)
        } else {
          return timestampFormat(time)
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.container-box{
  padding-left:20px;
}
.card-warpper {
  display: flex;
  flex-wrap:wrap;
  margin-left: -10px;
}
.w7-card{
  position: relative;
  top:0;
  margin: 10px;
  width: 300px;
	height: 160px;
  transition: 0.2s;
  background: #e6f2ff;
  border: 1px solid #eeeeee;
  box-sizing: border-box;
  .wi {
    font-size: 20px;
    color: #989898;
  }
  &:hover {
    position: relative;
    cursor:pointer;
    top:-10px;
    border-color: #bdddfd;
    .w7-card-title {
      color: #3296fa;
    }
    .icon-box{
      display:block;
    }
  }
  &-title {
    padding-top: 30px;
    padding-left: 30px;
    font-size: 16px;
    color: #4d4d4d;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  &-time {
    padding-top: 10px;
    padding-left: 30px;
    font-size: 12px;
    color: #a4a6a9;
  }
  .icon-box i:hover {
    color: #3296fa;
  }
  .el-icon-lock:hover {
    color: #ffffff;
  }
}
.card-box{
  margin-top:30px;
}
.icon-box{
  display:none;
  position:absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 8px 7px;
  text-align: right;
  background-color: #d8e7ff;
}
.icon-box i{
  position: relative;
  margin-left: 20px;
}
.icon-box i:hover .pos-box{
  display:block;
}
.icon-box i .pos-box{
  position: absolute;
  top:20px;
  left:-10px;
  display:none;
  text-align: center;
  width:40px;
  height:20px;
  line-height:16px;
  background: #000;
  color:#fff;
  font-size:14px;
}
.icon-box i .pos-box .arr-box{
  position: relative;
  width:100%;
  height:100%;
  box-sizing:border-box;
  background: #000;
  border:2px solid #000;
}
.icon-box i .arrow{
  width: 0px;
  height: 0px;
  position: absolute;
  border:5px solid transparent;
  border-bottom-color:#000;
  top:-10px;
  left:30%;
}
.add-btn{
  background:#fff;
  border:1px solid #eee;
  color:#b6b5b5;
  position: relative;
  .add-text {
    padding-top: 30px;
    padding-left: 30px;
    font-size: 16px;
    color: #4d4d4d;
  }
  i:hover {
    color: #b6b5b5;
  }
}
.add-box{
  font-size:60px;
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}
.ownership{
  margin-left:120px;
}
.el-dialog__footer{
  border-top:1px solid #eee;
}
.redBtn {
  color: #eb2e56;
}
.transfer-tip {
  display: flex;
  align-items: center;
  margin-top: -10px;
  margin-bottom: 15px;
  i {
    margin-right: 10px;
    font-size: 18px;
    color: #f76260;
  }
}
</style>