//url
export function getUrlParam(url, name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)")
  if (url && url.split('?')[1]) {
    var r = url.split('?')[1].match(reg)
    if (r != null) {
      return unescape(r[2])
    }
    return null
  }
}
//,paramName,replaceVal
export function replaceParamVal(url, paramName, replaceVal) {
  var oUrl = url.toString()
  var re=eval('/('+ paramName+'=)([^&]*)/gi')
  var nUrl = oUrl.replace(re,paramName+'='+replaceVal)
  return nUrl
}

//
export function timestampFormat( timeString ) {
  var timestamp = Date.parse(timeString)/1000
  function zeroize( num ) {
      return (String(num).length == 1 ? '0' : '') + num;
  }

  var curTimestamp = parseInt(new Date().getTime() / 1000); //
  var timestampDiff = curTimestamp - timestamp; // 

  var curDate = new Date( curTimestamp * 1000 ); // 
  var tmDate = new Date( timestamp * 1000 );  // 

  var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
  var H = tmDate.getHours(), i = tmDate.getMinutes();

  if ( timestampDiff < 60 ) { // 
    return "";
  } else if( timestampDiff < 3600 ) { // 
    return Math.floor( timestampDiff / 60 ) + "";
  } else if ( curDate.getFullYear() == Y && curDate.getMonth()+1 == m && curDate.getDate() == d ) {
    return '' + zeroize(H) + ':' + zeroize(i);
  } else {
    var oneDayDate = new Date( (curTimestamp - 86400) * 1000 ); // 
    if ( oneDayDate.getFullYear() == Y && oneDayDate.getMonth()+1 == m && oneDayDate.getDate() == d ) {
      return '' + zeroize(H) + ':' + zeroize(i);
    } else {
      return  Y + '' + zeroize(m) + '' + zeroize(d) + ' ' + zeroize(H) + ':' + zeroize(i);
    }
  }
}