<template>
    <div class="setting-nav">
      <div class="page-head"></div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="style">
          <div class="we7-panel-form">
            <div class="we7-panel-form__header"></div>
            <div class="we7-panel-form__body">
              <div class="we7-panel-form__item">
                <div class="we7-panel-form__label"></div>
                  <div class="we7-panel-form__value">{{styleData[style]}}</div>
                  <div class="we7-panel-form__action">
                    <el-tooltip effect="dark" content="" placement="bottom">
                      <i class="wi wi-edit wi-oper" @click="dialogStyle = true"></i>
                    </el-tooltip>
                  </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="menu">
          <div class="search-box">
            <div class="demo-input-btn">
              <el-button class="add-menu" type="primary" @click="openDialogMenu('add')"></el-button>
            </div>
          </div>
          <el-table class="w7-table" :data="menuList" ref="multipleTable"
            :header-cell-style="{background:'#f7f9fc',color:'#606266'}">
            <el-table-column label="" prop="name"></el-table-column>
            <el-table-column label="" prop="sort"></el-table-column>
            <el-table-column label="" align="right">
              <div class="oper" slot-scope="scope">
                <el-tooltip effect="dark" content="" placement="bottom">
                  <i class="wi wi-edit wi-oper" @click="openDialogMenu('edit', scope.row)"></i>
                </el-tooltip>
                <el-tooltip effect="dark" content="" placement="bottom">
                  <i class="wi wi-delete wi-oper" @click="delMenu(scope.row.id, scope.$index)"></i>
                </el-tooltip>
              </div>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog class="we7-dialog" title="" :visible.sync="dialogStyle" :close-on-click-modal="false" center>
        <el-form label-width="120px" label-position="left">
          <el-form-item label="">
            <el-select v-model="style">
              <el-option
                v-for="item in styleList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirmStyle"> </el-button>
          <el-button @click="dialogStyle = false"> </el-button>
        </div>
      </el-dialog>
      <el-dialog class="we7-dialog" :title="dialogMenuTitle" :visible.sync="dialogMenu" :close-on-click-modal="false" center>
        <el-form ref="menuForm" :model="menuData" :rules="rules" label-width="120px" label-position="left">
          <el-form-item label="" prop="name">
            <el-input v-model="menuData.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="menuData.sort"></el-input>
            <div class="we7-help-block"></div>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input v-model="menuData.url"></el-input>
            <div class="we7-help-block">httphttps</div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirm"> </el-button>
          <el-button @click="dialogMenu = false"> </el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
      return {
        activeName:'style',
        menuList: [],
        dialogStyle: false,
        style: '',
        styleData: {
          black: '',
          white: ''
        },
        styleList: [
          {
            value: 'white',
            label: ''
          }, {
            value: 'black',
            label: ''
          }
        ],
        dialogMenu: false,
        dialogMenuTitle: '',
        menuData: {},
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          sort: [
            { required: true, message: '', trigger: 'blur' }
          ],
          url: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    created() {
      this.getStyle()
      this.getMenuData()
    },
    methods: {
      getStyle() {
        this.$post('/admin/menu/get-theme')
          .then(res => {
            this.style = res || 'white'
          })
      },
      getMenuData() {
        this.$post('/admin/menu/all')
          .then(res => {
            this.menuList = res
          })
      },
      confirmStyle() {
        this.$post('/admin/menu/set-theme', {
          theme: this.style
        })
          .then(() => {
            this.$message('')
            this.dialogStyle = false
          })
      },
      openDialogMenu(type, row) {
        if (type == 'add') {
          this.dialogMenuTitle = ''
          this.menuData = {}
        } else {
          this.dialogMenuTitle = ''
          this.menuData = row
        }
        this.dialogMenu = true
      },
      confirm() {
        this.$refs.menuForm.validate((valid) => {
          if (valid) {
            if (this.dialogMenuTitle == '') {
              this.$post('/admin/menu/add', {
                ...this.menuData
              })
                .then(() => {
                  this.getData()
                  this.$message('')
                  this.dialogMenu = false
                })
            } else {
              this.$post('/admin/menu/update', {
                ...this.menuData
              })
                .then(() => {
                  this.getData()
                  this.$message('')
                  this.dialogMenu = false
                })
            }
          }
        })
      },
      delMenu(id, index) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$post('/admin/menu/delete', {
            id: id
          })
            .then(() => {
              this.menuList.splice(index, 1)
              this.$message('')
            })
        })
      },
      handleClick() {

      }
    }
}
</script>

<style lang="scss">
.setting-nav {
  .page-head {
    padding-bottom: 20px;
  }
  .we7-panel-form__header {
    margin-top: 5px;
  }
  .demo-input-btn {
    width: 100%;
    text-align: right;
    .add-menu {
      padding: 9px 30px;
    }
  }
  .w7-table {
    margin-top: 20px;
  }
}
</style>